import { Action } from '../interfaces/redux';
import { BarcodeState } from '../interfaces/barcode/barcode-state';

const defaultState: BarcodeState = {
  isDecoded: false,
  decodedOutput: null
};

export function barcodeReducer(state: BarcodeState = defaultState, action: Action) {
  switch(action.type) {
    case 'BARCODE_DECODED':
      // deal with immutable state
      state = JSON.parse(JSON.stringify(state));
      // modify state
      state.isDecoded = true;
      state.decodedOutput = action.payload;
      return state;
    case 'BARCODE_RESULT_RESET':
      // deal with immutable state
      state = JSON.parse(JSON.stringify(state));
      // modify state
      state.isDecoded = false;
      state.decodedOutput = null;
      return state;
    default:
      return state;
  }
}


