import { Component } from '@angular/core';
import Quagga from 'quagga';
import { AlertController } from 'ionic-angular';
import { BarcodeService } from '../../services/barcode.service';

/**
 * Generated class for the BarcodeReaderComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'barcode-reader',
  templateUrl: 'barcode-reader.html',
  styleUrls: [ '/src/components/barcode-reader/barcode-reader.scss' ]
})
export class BarcodeReaderComponent {

  isBarcodeReaderStopped: boolean = false;

  constructor(private alertCtrl: AlertController, private barcodeService: BarcodeService) {
  }

  public startQuagga() {
    // check if hardware supports
    if(!navigator.mediaDevices || !(typeof navigator.mediaDevices.getUserMedia === 'function')) {
      this.isBarcodeReaderStopped = true;
      const alert = this.alertCtrl.create({
        title: 'Device don\'t suuport camera',
        subTitle: 'Please make sure your device have camera and allow camera access.',
        buttons: ['OK']
      });
      alert.present();
      return;
    }

    // viewport width
    const recordSreenWidth = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
    // viewport height remove header-bar height = content height
    const recordScreenHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0) - 56;
    Quagga.init({
      inputStream: {
        name: "Live",
        type: "LiveStream",
        target: '#recordScreen',
        constraints: {
          width: recordSreenWidth,
          height: recordScreenHeight
        }
      },
      decoder: {
        readers: [ 'ean_reader' ],
        locator: {
          halfSample: true,
          patchSize: 'large'
        }
      }
    }, (error) => {
      if(error) { console.log(error); this.isBarcodeReaderStopped = true; }
      Quagga.onDetected(data => {
        this.barcodeService.barcodeDecoded(data);
      });
      Quagga.start();
      this.isBarcodeReaderStopped = false;
    });
  }

  public stopQuagga():void {
    Quagga.stop();
    this.isBarcodeReaderStopped = true;
  }

}



