import { Component } from '@angular/core';

import { BarcodeService } from '../../services/barcode.service';
import { Observable } from 'rxjs/Observable';
import { AppStore } from '../../store/interfaces/app-store';
import { Store, select } from '@ngrx/store';
import { BarcodeState } from '../../store/interfaces/barcode/barcode-state';
import 'rxjs/add/operator/map';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'page-barcode-detail',
  templateUrl: 'barcode-detail.html',
})
export class BarcodeDetailPage {

  barcodeState: Observable<BarcodeState>;
  barcodeStateSubscription: Subscription;
  decodedBarcode: string;

  constructor(private store: Store<AppStore>, private barcodeService: BarcodeService) {
    this.barcodeState = this.store.pipe(select('barcode'));
    this.barcodeStateSubscription = this.barcodeState.subscribe(data => {
      if(data.isDecoded) {
        this.decodedBarcode = data.decodedOutput.codeResult.code;
      }
    });
  }

  ionViewWillLeave() {
    this.barcodeService.barcodeResultReset();
    this.barcodeStateSubscription.unsubscribe();
  }
}



